@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700,700i&display=swap);
.App {
  text-align: center;
  padding: 20px 0;
}

.Thread form div {
  margin-top: 1em;
}
.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.Comment {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	
	margin-top: 15px;
	margin-bottom: 15px;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}

.top-comment {
	margin-top: 40px;
	width: 780px;
	overflow-y: auto;
	height: 425px;
	background-color: rgb(248, 248, 248);
}
.left-box {
	width: 21px;
	margin: 0;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0;
	padding-right: 0;
	padding-top: 0;
	margin: 0;
}
.Comment * {
	line-height: 21px;
	word-break: break-word;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}
.ql-editor {
	outline: none;
	white-space: pre-wrap;
}
